'use client';

import Image from 'next/image';
import { FaHeart } from 'react-icons/fa';

export default function ProductCard({ product }: { product: any }) {
  return (
    <div className="w-full max-w-xs bg-white rounded-md border hover:shadow-lg transition duration-300 cursor-pointer">
      <div className="flex justify-between items-start px-3 pt-3 ">
        <span className="text-xs bg-gray-800 text-white px-2 py-0.5 rounded-md">9% OFF</span>
        <FaHeart className="text-gray-500 hover:text-red-500 cursor-pointer" />
      </div>

      <div className="w-full p-3">
        <Image
          src={product.image}
          alt={product.name}
          width={300}
          height={300}
          className="w-full h-auto object-contain"
        />
      </div>

      <div className="px-3 pb-3">
        <h3 className="text-sm font-medium text-gray-800 truncate">{product.name}</h3>
        
        <div className="text-sm mt-1">
          <span className="line-through text-gray-400">₹ {product.originalPrice.toLocaleString()}</span>
          <span className="ml-2 font-semibold text-black">₹ {product.salePrice.toLocaleString()}</span>
        </div>

        <div className="text-xs text-white bg-black inline-block mt-1 px-2 py-0.5 rounded-full">
          SAVE ₹ {product.savings.toLocaleString()}
        </div>

        <div className="flex gap-1 mt-2">
          {product.colors.map((color: string, idx: number) => (
            <span
              key={idx}
              className={`w-5 h-5 rounded-full border-2 border-gray-200`}
              style={{ backgroundColor: color }}
            ></span>
          ))}
        </div>
      </div>
    </div>
  );
}
